.button-theme {
    width: 50px;
    color: var(--text-main-color);
    font-size: 20px;
    cursor: pointer;
    @include respond(s) {
        font-size: 35px;
    }
    &_icon-wrapper {
        animation: button-theme-in 1s ease forwards;
        &-hidden {
            animation: button-theme-out 1s ease forwards;
            &-static {
                position: absolute;
                pointer-events: none;
                opacity: 0;
            }
        }
    }
}
