@keyframes button-theme-in {
    0% {
        transform: translateY(-150%);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}
@keyframes button-theme-out {
    0% {
        transform: translateY(0);
        opacity: 1;
    }
    100% {
        transform: translateY(150%);
        opacity: 0;
    }
}
@keyframes enter-opacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes rotate-center {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes tracking-in-expand {
    0% {
        letter-spacing: -0.5em;
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes tracking-in-expand2 {
    0% {
        letter-spacing: 0.5em;
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes roll-in-left {
    0% {
        -webkit-transform: translateX(-150px) rotate(-180deg);
        transform: translateX(-150px) rotate(-180deg);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateX(0) rotate(0deg);
        transform: translateX(0) rotate(0deg);
        opacity: 1;
    }
}
@keyframes color-change-1 {
    0% {
        background-color: rgba(228, 7, 7, 0.5);
        width: 65cqmin;
        height: 65cqmin;
        border-radius: 50%;
    }
    20% {
        background-color: #fcec079c;
        width: 67cqmin;
        height: 67cqmin;
        border-radius: 45%;
    }
    40% {
        background-color: #5cef0792;
        width: 65cqmin;
        height: 65cqmin;
        border-radius: 40%;
    }
    60% {
        background-color: #084bda98;
        width: 67cqmin;
        height: 67cqmin;
        border-radius: 45%;
    }
    80% {
        background-color: #cb0ae594;
