@keyframes slide-in-top {
    0% {
        -webkit-transform: translateY(-100dvh);
        transform: translateY(-100dvh);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
        opacity: 1;
    }
}
@keyframes slide-out-top {
    0% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: translateY(-100dvh);
        transform: translateY(-100dvh);
        opacity: 0;
    }
}
@keyframes slide-in-left {
    0% {
        -webkit-transform: translateX(-30cqw);
        transform: translateX(-30cqw);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes slide-in-right {
    0% {
        -webkit-transform: translateX(30cqw);
        transform: translateX(30cqw);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes contacts-roll {
    0% {
        -webkit-transform: translateX(100cqw) rotate(720deg);
        transform: translateX(100cqw) rotate(720deg);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateX(0) rotate(0deg);
        transform: translateX(0) rotate(0deg);
        opacity: 1;
    }
}
