.decorativeSeparator {
    color: var(--text-main-color);
    display: grid;
    grid-template-columns: 1fr min-content 1fr;
    align-items: center;
    column-gap: 2cqmin;
    padding: 4cqmax 0;
    &_border {
        height: 2px;
        &.left {
            background: linear-gradient(
                to left,
                var(--text-main-color) 0%,
                rgba(0, 0, 0, 0) 70%
            );
        }
        &.right {
            background: linear-gradient(
                to right,
                var(--text-main-color) 0%,
                rgba(0, 0, 0, 0) 70%
            );
        }
    }
    &_text {
        display: block;
        font-family: "Montserrat";
        font-weight: 300;
        font-size: clamp(20px, 4.5cqmin, 45px);
        width: min-content;
        text-align: center;
    }
}
